/*ABOUT ME*/
html, body {
  width: 100%;
  height: 100%;
  margin: 0px;
  padding: 0;
  background-color: black;
}


.loding {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 50px;
  height: 50px;
  border: 4px solid #fff;
  border-bottom: 4px solid transparent;
  border-radius: 50%;
  animation: animate 1s linear infinite;
}

@keyframes animate {
  0%{
    transform: translate(-50%, -50%) rotate(0deg);
  }
  100%{
    transform: translate(-50%, -50%) rotate(360deg);
  }
}